/**
 *
 * @title 
 * @description labelvalue
 *
 */

import React, { Component } from 'react';
import { Row, Col } from 'bee-layout';
import Cascader from '../../src';
import FormControl from 'bee-form-control'
import Form from 'bee-form';
const FormItem = Form.FormItem;

const options = [{
	label: '',
	value: 'jczj',
	children: [{
		label: '',
		value: 'dh',
		children: [{
			label: '',
			value: 'mbx'
		}, {
			label: '',
			value: 'fy'
		}, {
			label: '',
			value: 'bq'
		}, {
			label: '',
			value: 'cd'
		}]
	}, {
		label: '',
		value: 'fk',
		children: [{
			label: '',
			value: 'mtk'
		}, {
			label: '',
			value: 'tz'
		}]
	},
		{
			label: '',
			value: 'bd'
		}]
}, {
	label: '',
	value: 'yyzj',
	children: [{
		label: '',
		value: 'ref',
		children: [{
			label: '',
			value: 'reftree'
		}, {
			label: '',
			value: 'reftable'
		}, {
			label: '',
			value: 'reftransfer'
		}]
	}]
}
];

const defaultOptions = [{
	label: '',
	value: 'jczj',
}, {
	label: '',
	value: 'dh',
}, {
	label: '',
	value: 'cd',
}];
const defaultOptions1 = ["yyzj", "ref", "reftransfer"]


class Demo2 extends Component {
	onChange = (value, selectedOptions) => {
		console.log(value, selectedOptions);
	}

	onSubmit = () => {
		this.props.form.validateFields((err, _values) => {
			console.log(_values);
		});
	}
	onRest = () => {
		this.props.form.resetFields();
		this.props.form.validateFields((err, _values) => {
			console.log(_values);
		});
	}

	render() {
		let { form: { getFieldProps } } = this.props;
		return (
			<div>
				<Row>
					<Col md={4}>
						<p>Cascader</p>
						<div className="height-150" style={{paddingLeft:'12px'}}>
							<Cascader
								defaultValue={defaultOptions}
								options={options}
								onChange={this.onChange}
								placeholder=""
							/>
						</div>
					</Col>
				</Row>
				<Row>
					<Col md={4}>
						<div className="height-150">
							<p>formCascader</p>
							<FormItem onSubmit={this.onSubmit}>
								<Cascader
									options={options}
									placeholder=""
									{...getFieldProps('area', { initialValue: defaultOptions1, onChange: this.onChange})}
								/>
							</FormItem>
							<div style={{paddingLeft:'12px'}}>
								<button onClick={this.onSubmit}></button>
								<button onClick={this.onRest}></button>
							</div>
						</div>
					</Col>
				</Row>
			</div>
		)
	}
}
export default Form.createForm()(Demo2);



