/**
 *
 * @title 
 * @description `disabled`
 *
 */

import React, { Component } from 'react';
import { Row, Col } from 'bee-layout';
import Cascader from '../../src';

const options = [{
    label: '',
    value: 'zj',
    children: [{
            label: '',
            value: 'hz',
            children: [{
                label: '',
                value: 'xh',
                children: [{
                    label: '',
                    value: 'bnz'
                },{
                    label: '',
                    value: 'xx'
                }]
            }]
        }]
    },
    {
        label: '',
    value: 'js',
    children: [{
            label: '',
            value: 'nj',
            children: [{
                label: '',
                value: 'zhm'
            }]
        }]
    },
    {
        label: '',
    value: 'sd'
    }
];

class Demo4 extends Component {  
    render(){
        return (
            <Row>
                <Col md={4}>
                    <div className="height-150">
                        <Cascader disabled options={options} placeholder=""/>
                    </div>
                </Col>
            </Row>
        )
    }
}

export default Demo4;


