/**
 *
 * @title options
 * @description `options`
 *
 */

import React, { Component } from 'react';
import { Row, Col } from 'bee-layout';
import Button from 'bee-button';
import Cascader from '../../src';

const baseOptions = [{
    label: '',
    value: 'jczj',
    children: [{
        label: '',
        value: 'dh',
        children: [{
            label: '',
            value: 'mbx'
        },{
            label: '',
            value: 'fy'
        },{
            label: '',
            value: 'bq'
        },{
            label: '',
            value: 'cd'
        }]
    },{
        label: '',
        value: 'fk',
        children: [{
            label: '',
            value: 'mtk'
        },{
            label: '',
            value: 'tz'
        }]
        },
        {
        label: '',
            value: 'bd'
        }]
    },{
        label: '',
        value: 'yyzj',
        children: [{
            label: '',
            value: 'ref',
            children: [{
                label: '',
                value: 'reftree'
            },{
                label: '',
                value: 'reftable'
            },{
                label: '',
                value: 'reftransfer'
            }]
        }]
    }
];

class Demo6 extends Component {  
    state = {
        options: baseOptions
    }
    
    changeOptions = () => {
        this.setState({
            options: [{
                label: '',
                value: 'bj',
                children: [{
                        label: '',
                        value: 'gg',
                    },{
                        label: '',
                        value: 'tt',
                    },{
                        label: '',
                        value: 'wfj',
                    }]
                },
                {
                    label: '',
                    value: 'js',
                    children: [{
                            label: '',
                            value: 'nj',
                            children: [{
                                label: '',
                                value: 'zhm'
                            }]
                        }]
                    },
                    {
                        label: '',
                    value: 'sd'
                }
            ]
        })
    }

    render(){
        return (
            <Row>
                <Col md={4}>
                    <div className="height-150">
                        <Button colors="primary" onClick={this.changeOptions} style={{marginBottom:8+'px'}}></Button>
                        <Cascader options={this.state.options} placeholder=""/>
                    </div>
                </Col>
            </Row>
        )
    }
}

export default Demo6;


