/**
 *
 * @title 
 * @description  `inputValue`  onChange 
 *
 */

import React, { Component } from 'react';
import { Row, Col } from 'bee-layout';
import Cascader from '../../src';

const options = [{
	label: '',
	value: 'jczj',
	children: [{
		label: '',
		value: 'dh',
		children: [{
			label: '',
			value: 'mbx'
		},{
			label: '',
			value: 'fy'
		},{
			label: '',
			value: 'bq'
		},{
			label: '',
			value: 'cd'
		}]
	},{
		label: '',
		value: 'fk',
		children: [{
			label: '',
			value: 'mtk'
		},{
			label: '',
			value: 'tz'
		}]
 	},
 	{
		label: '',
 	    value: 'bd'
 	}]
	},{
		label: '',
		value: 'yyzj',
		children: [{
			label: '',
			value: 'ref',
			children: [{
				label: '',
				value: 'reftree'
			},{
				label: '',
				value: 'reftable'
			},{
				label: '',
				value: 'reftransfer'
			}]
		}]
	}
];
class Demo8 extends Component {
	constructor(props){
		super(props);
		this.state = {
			inputValue:""
		}
	}

	onChange = (value, selectedOptions) => {
        console.log(value, selectedOptions);
        // 
        let lastLabel = '';
        if (typeof selectedOptions !== 'undefined') {
            lastLabel = selectedOptions[selectedOptions.length - 1].label;
        }
        this.setState({ 
            inputValue : lastLabel
        })
	}
	
 	render(){
 		return(
			<Row>
				<Col md={4}>
					<div className="height-150">
						<Cascader 
							options = {options} 
							onChange = {this.onChange}
							placeholder = ""
							inputValue = {this.state.inputValue}
						/>
					</div>
				</Col>
			</Row>
 		)
 	}
}
export default Demo8;


