import React, { Component } from 'react';
import { Row, Col } from 'bee-layout';
import Cascader from '../../src';

const options = [{
	label: '',
	value: 'jczj',
	children: [{
		label: '',
		value: 'dh',
		children: [{
			label: '',
			value: 'mbx'
		},{
			label: '',
			value: 'fy'
		},{
			label: '',
			value: 'bq'
		},{
			label: '',
			value: 'cd'
		}]
	},{
		label: '',
		value: 'fk',
		children: [{
			label: '',
			value: 'mtk'
		},{
			label: '',
			value: 'tz'
		}]
 	},
 	{
		label: '',
 	    value: 'bd'
 	}]
	},{
		label: '',
		value: 'yyzj',
		children: [{
			label: '',
			value: 'ref',
			children: [{
				label: '',
				value: 'reftree'
			},{
				label: '',
				value: 'reftable'
			},{
				label: '',
				value: 'reftransfer'
			}]
		}]
	}
];

class Demo5 extends Component {  
    render(){
        return (
            <Row>
                <Col md={4}>
                    <div className="height-150">
                        <Cascader changeOnSelect options={options} placeholder=""/>
                    </div>
                </Col>
            </Row>
        )
    }
}

export default Demo5;


